import React, { useState, useEffect } from 'react';
import './App.css';
import { getNumbers } from './utils';
import { Pagination } from './components/Pagination/Pagination';

// eslint-disable-next-line @typescript-eslint/no-unused-vars
const items = getNumbers(1, 42)
  .map(n => `Item ${n}`);

export const App: React.FC = () => {
  const [itemsToRender, setItemsToRender] = useState(items);
  const [perPage, setPerPage] = useState(5);
  const [currentPage, setCurrentPage] = useState(1);
  const [leadNumbers, setLeadNumbers] = useState('');
  const total = items.length;

  const onPageChange = (page: number) => {
    setCurrentPage(() => page);
  };

  const onPerPageChange = (value: number) => {
    setPerPage(value);
    setCurrentPage(() => {
      return 1;
    });
  };

  useEffect(() => {
    const value1 = (currentPage - 1) * perPage + 1;
    const value2 = value1 + perPage - 1;

    setItemsToRender(items.slice(value1 - 1, value2));
    setLeadNumbers(`${value1} - ${value2 > total ? total : value2}`);
  }, [currentPage, perPage]);

  return (
    <div className="container">
      <h1>Items with Pagination</h1>

      <p className="lead" data-cy="info">
        {`Page ${currentPage} (items ${leadNumbers} of ${total})`}
      </p>

      <div className="form-group row">
        <div className="col-3 col-sm-2 col-xl-1">
          <select
            data-cy="perPageSelector"
            id="perPageSelector"
            className="form-control"
            value={perPage}
            onChange={(e) => onPerPageChange(+e.target.value)}
          >
            <option value="3">3</option>
            <option value="5">5</option>
            <option value="10">10</option>
            <option value="20">20</option>
          </select>
        </div>

        <label htmlFor="perPageSelector" className="col-form-label col">
          items per page
        </label>
      </div>

      <Pagination
        total={total}
        perPage={perPage}
        currentPage={currentPage}
        onPageChange={onPageChange}
      />

      <ul>
        {itemsToRender.map((item, i) => (
          i < perPage
          && <li data-cy="item" key={item}>{item}</li>
        ))}
      </ul>
    </div>
  );
};

export default App;
